.about {
  &__title {
    margin: 0 0 32px;

    font-weight: 500;
    font-size: 32px;
    line-height: 32px;

    letter-spacing: -1.5px;
    text-transform: capitalize;

    color: $white;

    @include onLaptop {
      font-size: 48px;
      line-height: 48px;
    }

    @include onDesktop {
      font-size: 64px;
      line-height: 64px;
    }
  }

  &__paragraph {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;

    color: $white;
    margin: 0;
  }
}
